/*/////////////////////////////////////////////////////////////////////////////////////////music.css\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\*/

.siteBody{
	background: black;
}

.siteBody div {
	display: flex;

}


/*00a8af - color img */


.musicBlock{
	margin: 15px 15px 0 0 ;
	display: flex;
	flex-direction: column;
	width:100%;
	height: 100%;
}

.react-audio-player{}

.curentMusic, .fullListMusic{
	border-radius: 1px;

}

.curentMusic{

	align-items: center;
	display: flex;
	width: 685px;
	height: 60px;
	background: #F0F2F4;

}



#about_current_music > div {
	font-size: 15px;
	margin-left: 5px;
	display: flex;
	flex-direction: column;
}


#about_current_music div p:nth-child(2){
	color: #a5a5a5;
}



.fullListMusic{
	display: flex;
	flex-direction: column;
	width: 685px;
	margin-top: 15px;
	min-width: 550px;
	background: white;
}

.category{
	margin-left: 15px;
	display: flex;
}


.category a {
	color: black;
	text-decoration: none;
	margin: 0 5px;
 	padding: 15px;
 	cursor: pointer;
}




hr {
	width: 400px;
	display: flex;
	margin: 0 0 0 5px;

}

.showlist{
	display: flex;
	flex-direction: column;
}

.showlist > div + div > div{
	display: flex;
	flex-direction: column;
}

.search {
	margin: 5px;

}

.search input{
	padding: 8px 70px 8px 12px;
	height: 15px;
	width: 400px;
}

.search button{
	margin-left: 5px;
	width: 50px;
	background: url("/assets/img/music/serch.png") no-repeat 50%;
		background-size: 20px 20px;
}

.about_list{
	width: 530px;
	display: flex;
	justify-content: space-between;
    font-size: 15px;
    margin:5px 5px;
}

.list_music{
	width: 550px;
	display: flex;
	flex-direction: column;
}

li{
	cursor: pointer;
	list-style-type: none;
}

ul{
	padding-left: 0;
}
